@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@600&family=Roboto:wght@300;400&display=swap);
html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

h1 {
  font-size: 2.4rem;
  font-family: "Baloo Tammudu 2", cursive;
}

.App {
  padding: 20px;
}

#home-btn {
  color: white;
  text-decoration: none;
}

button {
  font-family: "Roboto", sans-serif;
  background-color: #333333;
  color: #fff;
  border: none;
  margin: 2vh 0;
  width: 80vw;
  height: 10vh;
  font-size: 1.5rem;
  border-radius: 10px;
}

button:hover {
  cursor: pointer;
  background-color: #747474;
}

input {
  font-family: "Roboto", sans-serif;
  background-color: #dadada;
  color: #222222;
  border: none;
  margin: 2vh 0;
  width: 80vw;
  height: 10vh;
  font-size: 1.5rem;
  border-radius: 10px;
  padding-left: 2vw;
}

.alert-container {
  width: 100vw;
  height: 92vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.alert-button {
  width: 50vh;
  height: 50vh;
  border-radius: 50%;
  background-color: red;
  font-size: 4rem;
  text-transform: uppercase;
}

.alert-button:hover {
  background-color: rgb(255, 62, 62);
}

.alert-button:active {
  transform: scale(0.98);
}

.alert-button:focus {
  outline: none;
  border: none;
}

.alerth1 {
  margin: 0;
}

.iamokflex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.iamokform {
  width: 90vw;
  justify-content: space-between;
}

.yesorno {
  flex-direction: row;
}

.yesorno > label {
  width: 70%;
}

.yesorno > select {
  width: 10%;
  height: 4vh;
}

.checkboxes {
  margin-top: 5vh;
  flex-direction: column;
  width: 100%;
}

.checkboxes > label {
  display: flex;
  align-items: center;
  justify-content: left;
  height: 5vh
}

.checkboxes > label > input {
  height: 4vh;
  width: 5vw;
  margin-right: 5vw;
}

.conditionalform > textarea {
  width: 65vw;
  height: 10vh;
}
.user-alert {
  height: 87vh;
  margin-top: 5vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content {
  overflow: hidden;
}

.dot {
  animation-name: blink;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  background-color: red;
  height: 8vh;
  width: 8vh;
  border-radius: 50%;
  margin: 0 auto;
  display: inline-block;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

h2 {
  font-size: 3rem;
  line-height: 1rem;
  font-weight: 900;
}

h4 {
  font-size: 1.5rem;
  font-weight: 400;
}

.button-big {
  font-size: 2rem;
  line-height: 0rem;
}

.button-baseline {
  line-height: 0;
  font-size: 1.2rem;
}

.cancel-alert,
.finish-alert {
  width: 40vw;
  padding-bottom: 3vh;
}

.buttons-div {
  display: flex;
  justify-content: space-around;
  margin-bottom: 3vh;
}

.authform {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 95vh;
}

h1 {
  text-align: center;
}

.formInputs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 80vw;
}

.authbuttons {
  display: flex;
  flex-direction: column;
  margin-bottom: 8vh;
}

.alreadymember {
  width: 60vw;
  height: 8vh;
  font-size: 1rem;
  background-color: #888888;
}

.map-container {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

.text {
  position: absolute;
  right: 0;
}

.alert-list-container{
  padding-top: 5vh;
  width: 80%;
  margin: 0 auto;
}

.alert-item{
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(165, 165, 165);
  padding-bottom: 4vh;
}

.alert-item-text{ 
  width: 50%;
  margin-left: 3vh;
}

.alert-item-text > h3 {
  color: rgb(173, 35, 35);
  font-size: 1.8rem;
}

.alert-text-nets {
  font-weight: 600;
}

.alert-list-btn{
  width: 50%;
  margin-top: 10vh;
}

.alert-list-btn > a > button {
  height: 8vh;
  width: 80%;
  margin: 0 auto;
  background-color: rgb(173, 35, 35);
}


.nets-list-container {
  width: 90%;
  margin: 0 auto;
}

.net-card {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgb(165, 165, 165);
  padding-bottom: 4vh;
}

.net-card-info {
  width: 50%;
}

.netcode {
  font-size: 1.3rem;
  font-weight: 500;
  letter-spacing: 0.3px;
}

.net-card-btn {
  width: 30%;
}

.net-card-btn > button {
  height: 8vh;
  width: 100%;
}

.joinorcreatebtn {
  margin: 8vh 2.5vh;
}

.map-container {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

.singleMarker {
  padding: 100px;
  background-color: rgba(0, 0, 255, 0.1);
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 255, 0.5);
}

.alert-user-info {
  width: 100vw;
  height: 8vh;
  margin: 0;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.6);
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  color: black;
}

.alert-user-info > a {
  background-color: #222222;
  color: #fff;
  text-decoration: none;
  padding: 1vh 3vw;
  border: none;
  border-radius: 5px;
}

.alert-bar-text {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.alert-bar-username {
  font-weight: 600;
}

.alert-bar-text > p {
  margin: 1vw;
}

#single-alert-map > .mapboxgl-canvas-container > .mapboxgl-marker {
  padding: 15vh;
  border: 1px solid red;
  background-color: rgba(255, 0, 0, 0.2);
  border-radius: 50%;
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 5vw;
  background: #2e2e2e;
  height: 8vh;
  color: white;
}

.navbar > a {
  text-decoration: none;
  color: #fff;
}

.username {
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 1.1rem;
}

.notif-num {
  width: 4vh;
  height: 4vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  background-color: red;
  border-radius: 50%;
}

.notif-num:hover {
  cursor: pointer;
}

.notif-num > p {
  margin-top: 18px;
}

.zero {
  background-color: rgb(128, 128, 128);
}

.fas {
  font-size: 1.4rem;
  padding: 10px;
}

.fas:hover {
  cursor: pointer;
}

.flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.menu {
  background: #2e2e2e;
  height: 95vh;
  width: 100vw;
  color: white;
  position: absolute;
  top: 5vh;
  left: 0;
  right: 0;
  bottom: 0;
  font-family: "Roboto", sans-serif;
  justify-content: space-around;
  z-index: 10;
}

.menu-button {
  background-color: #2e2e2e;
}

