@import url("https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@600&family=Roboto:wght@300;400&display=swap");

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

h1 {
  font-size: 2.4rem;
  font-family: "Baloo Tammudu 2", cursive;
}

.App {
  padding: 20px;
}

#home-btn {
  color: white;
  text-decoration: none;
}

button {
  font-family: "Roboto", sans-serif;
  background-color: #333333;
  color: #fff;
  border: none;
  margin: 2vh 0;
  width: 80vw;
  height: 10vh;
  font-size: 1.5rem;
  border-radius: 10px;
}

button:hover {
  cursor: pointer;
  background-color: #747474;
}

input {
  font-family: "Roboto", sans-serif;
  background-color: #dadada;
  color: #222222;
  border: none;
  margin: 2vh 0;
  width: 80vw;
  height: 10vh;
  font-size: 1.5rem;
  border-radius: 10px;
  padding-left: 2vw;
}
